import * as core from "@actions/core";
import { context, getOctokit } from "@actions/github";
import { SitemapStream, streamToPromise } from "sitemap";
import { Readable } from "stream";
import "../ui/utilities/i18n";
import { loadApps } from "./loadApps";
import { shuffle } from "../utilities/array";
import { getLastMod } from "../utilities/getLastMod";
import { sortBy } from "lodash";
const lastUpdate = new Date("2025-02-04");
/**
 * The main function for the action.
 * @returns Resolves when the action is complete.
 */
export async function run() {
    try {
        let apps = await loadApps();
        await firstCrawled(apps);
        shuffle(apps);
        apps = apps.sort(function (a, b) {
            return b.score - a.score;
        });
        apps.forEach((app) => {
            delete app.score.details;
        });
        await uploadToRepo("docs/api/apps/all.json", JSON.stringify(apps), "Update app catalog", core.getInput("ghToken"));
        await uploadToRepo("docs/sitemap.xml", await generateSitemap(apps), "Update sitemap", core.getInput("ghToken"));
    }
    catch (error) {
        // Fail the workflow run if an error occurs
        if (error instanceof Error)
            core.setFailed(error.message);
    }
}
async function firstCrawled(apps) {
    const now = new Date().toISOString();
    const knownApps = (await (await fetch("https://osm-apps.zottelig.ch/api/apps/all.json", {})).json());
    for (const app of apps) {
        const knownApp = knownApps.find((k) => k.id === app.id);
        if (!knownApp) {
            app.source = app.source.map((s) => ({ ...s, firstCrawled: now }));
        }
        else {
            for (const source of app.source) {
                const knownSource = knownApp.source.find((k) => k.name === source.name && k.url === source.url);
                if (!knownSource) {
                    source.firstCrawled = now;
                }
                else {
                    source.firstCrawled = knownSource.firstCrawled || "2025-03-01T00:00:00Z";
                }
            }
        }
        app.source = sortBy(app.source, getLastMod).reverse();
    }
}
async function generateSitemap(apps) {
    // An array with your links
    const links = [];
    links.push({
        url: "https://osm-apps.zottelig.ch",
        priority: 1.0,
        lastmod: lastUpdate,
    });
    links.push({
        url: "https://osm-apps.zottelig.ch/docs/",
        priority: 0.9,
        lastmod: lastUpdate,
    });
    links.push({
        url: "https://osm-apps.zottelig.ch/?category=focus",
        priority: 0.8,
        lastmod: lastUpdate,
    });
    links.push({
        url: "https://osm-apps.zottelig.ch/?category=latest",
        priority: 0.8,
        lastmod: lastUpdate,
    });
    links.push({
        url: "https://osm-apps.zottelig.ch/?category=mobile",
        priority: 0.8,
        lastmod: lastUpdate,
    });
    links.push({
        url: "https://osm-apps.zottelig.ch/?category=navigation",
        priority: 0.8,
        lastmod: lastUpdate,
    });
    links.push({
        url: "https://osm-apps.zottelig.ch/?category=edit",
        priority: 0.8,
    });
    links.push(...apps.map((app) => ({
        url: `https://osm-apps.zottelig.ch/?app=${app.id}`,
        priority: (app.score / 10) * 0.5 + 0.1,
        lastmod: lastUpdate > new Date(getLastMod(app.source[0]))
            ? lastUpdate
            : new Date(getLastMod(app.source[0])),
    })));
    // Create a stream to write to
    const stream = new SitemapStream({
        hostname: "https://osm-apps.zottelig.ch",
    });
    // Return a promise that resolves with your XML string
    const data = await streamToPromise(Readable.from(links).pipe(stream));
    return data.toString();
}
async function uploadToRepo(filePath, content, commitMessage, ghToken) {
    if (!ghToken) {
        throw new Error("GitHub token is required to upload files.");
    }
    const octokit = getOctokit(ghToken);
    const owner = context.repo.owner;
    const repo = context.repo.repo;
    // JSON-Inhalt als Base64 kodieren
    const base64Content = Buffer.from(content).toString("base64");
    // Prüfen, ob die Datei existiert
    let sha;
    try {
        const { data } = await octokit.rest.repos.getContent({
            owner,
            repo,
            path: filePath,
        });
        if ("sha" in data) {
            sha = data.sha; // SHA der vorhandenen Datei speichern
        }
    }
    catch (error) {
        if (error?.status !== 404) {
            throw error; // Fehler weitergeben, falls es kein 404 ist
        }
    }
    // Datei erstellen oder aktualisieren
    await octokit.rest.repos.createOrUpdateFileContents({
        owner,
        repo,
        path: filePath,
        message: commitMessage,
        content: base64Content,
        sha,
    });
    console.log(`File "${filePath}" has been uploaded to the repository.`);
}
