function check(value) {
    return value?.match("(?:.*GPL.*|Apache.*|.*BSD.*|PD|WTFPL|ISC.*|MIT.*|Unlicense|ODbL.*|MPL.*|CC.*|Ms-PL.*)");
}
export function isFreeAndOpenSource(value) {
    if (!value) {
        return false;
    }
    if (typeof value === "string") {
        return check(value);
    }
    return !!value.find((v) => check(v));
}
